import React, { useEffect, useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import DragAndDropComponent from "../../components/DragAndDrop/DragAndDrop";
import { Image } from "react-bootstrap";
import "./ObsolescenceWidget.css";
import Loader from "../../components/Loader/Loader";
import ReusableTable from "../../components/Table/Table";
import CardWithDragAndDrop from "../../components/Card/cardwithdraganddrop";
import {
  setDroppedObjectData,
  setIsDropped,
  setPlantObjectData,
} from "../../store/droppedObjectSlice";
import useToast from "../../hooks/useToast";
import useObsolescenceDropableArea from "../../hooks/useObsolescenceDropableArea";
import ObsolescenceWidgetToolbarNativeCta from "./ObsolescenceWidgetToolbarNativeCta";
import { fetchData } from "../../utils/helpers";

const ObsolescenceWidget = () => {
  const { initializeDroppableArea, loading } = useObsolescenceDropableArea();
  const [tableKey, setTableKey] = useState(0);
  const [tableData, setTableData] = useState([]);
  const [isTableLoading, setIsTableLoading] = useState(false);
  const [screenLoader, setScreenLoader] = useState(false);
  const [isCardDataAvailable, setIsCardDataAvailable] = useState(false);
  const [selectableRows, setSelectableRows] = useState([]);
  const dispatch = useDispatch();
  const { showSuccessToast, showErrorToast } = useToast();

  // Redux selectors
  const droppedObjectData = useSelector(
    (state) => state.droppedObject.droppedObjectData
  );
  const loadingParentDetails = useSelector(
    (state) => state.droppedObject.loadingParentDetails
  );
  const isDropped = useSelector((state) => state.droppedObject.isDropped);
  const selectedTableRows = useSelector(
    (state) => state.droppedObject.selectedTableRows
  );

  // Extract type and state from droppedObjectData safely
  const type = droppedObjectData.initialDraggedData?.data?.items[0]?.objectType;
  const stateVal = droppedObjectData.cardData?.["Maturity State"];

  useEffect(() => {
    setIsTableLoading(loadingParentDetails);
  }, [loadingParentDetails]);

  // Columns adapted to API response (id, name, type, modified, created)
  const columns = useMemo(() => {
    return [
      { Header: "ID", accessor: "id" },
      { Header: "Name", accessor: "name" },
      { Header: "Type", accessor: "type" },
      { Header: "Modified", accessor: "modified" },
      { Header: "Created", accessor: "created" },
    ];
  }, []);

  // Fetch function for your API to get data (example payload & URL)
  const fetchTableDataFromAPI = async () => {
    setIsTableLoading(true);
    const API_URL =
      `${process.env.REACT_APP_3DSPACE}/enovia/resources/v1/modeler/dseng/dseng:EngItem/locate/`;
    const payload = {
      referencedObjects: [
        {
          source: `${process.env.REACT_APP_3DSPACE}/enovia`,
          type: "dseng:EngItem",
          identifier: "7D56FB4BE1D72000683006B8000035C5",
          relativePath:
            "/resources/v1/modeler/dseng/dseng:EngItem/7D56FB4BE1D72000683006B8000035C5",
        },
      ],
    };

    try {
      const response = await fetch(API_URL, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          SecurityContext:
            "VPLMProjectAdministrator.BU-0000001.Rosemount Measurement", // Replace with your SecurityContext
          ENO_CSRF_TOKEN: "QMF8-WHQB-FW5H-K4JL-UCAI-TC8T-1TKW-NH5K", // Replace with your CSRF token
        },
        body: JSON.stringify(payload),
      });

      if (!response.ok) throw new Error("API fetch failed");

      const data = await response.json();

      const engInstances = data.member?.[0]?.["dseng:EngInstance"]?.member || [];

      // Map API data to your tableData format
      const formattedData = engInstances.map((item) => ({
        id: item.id,
        name: item.name,
        type: item.type,
        modified: item.modified,
        created: item.created,
        changedCells: {}, // optional for tracking changes
      }));

      setTableData(formattedData);
      setIsTableLoading(false);
    } catch (error) {
      console.error("Error fetching API data:", error);
      setTableData([]);
      setIsTableLoading(false);
    }
  };

  // Fetch API data when widget loads and when isDropped is true
  useEffect(() => {
    if (isDropped) {
      fetchTableDataFromAPI();
    }
  }, [isDropped]);

  // Save handler (you can adapt your existing handleSave logic here)
  const handleSave = async () => {
    setScreenLoader(true);

    // For demo: just print the current tableData
    console.log("Saving data:", tableData);

    // Implement your actual save logic here or call your existing handleSave

    setScreenLoader(false);
    showSuccessToast("Data saved successfully!"); // Or handle errors accordingly
  };

  // Handle mass update example (your existing logic)
  const handleMassUpdate = (data, column, value) => {
    const updatedData = data.map((row) => ({
      ...row,
      [column]: value,
    }));

    // Merge updatedData into tableData
    const mergedData = tableData.map((row) => {
      const updatedRow = updatedData.find((u) => u.id === row.id);
      return updatedRow || row;
    });

    setTableData(mergedData);
  };

  // Home button handler (reset everything)
  const handleHomeClick = () => {
    initializeDroppableArea();
    dispatch(setIsDropped(false));
    dispatch(
      setDroppedObjectData({
        cardData: {},
        parentDetails: [],
        versions: [],
        initialDraggedData: [],
      })
    );
    dispatch(
      setPlantObjectData({
        allPlants: [],
        initialAssignedPlants: [],
        uniquePlants: [],
        productChildren: [],
        CAName: false,
        headers: {},
      })
    );
    setTableData([]);
    setIsCardDataAvailable(false);
  };

  // Prepare cardData from droppedObjectData for CardWithDragAndDrop
  const cardData = useMemo(() => {
    if (!droppedObjectData) return null;
    return {
      ...droppedObjectData.cardData,
    };
  }, [droppedObjectData]);

  useEffect(() => {
    setIsCardDataAvailable(!!cardData);
  }, [cardData]);

  return (
    <>
      {screenLoader && (
        <div className="loading-overlay">
          <Loader />
          <p>Saving...</p>
        </div>
      )}
      {!isDropped && !loading && !isTableLoading && <DragAndDropComponent />}
      {loading && <Loader />}
      {isDropped && (
        <>
          {loading && <Loader />}
          <div className="content-wrapper py-3 border-bottom">
            <div className="d-flex ">
              <div className=" p-0 pt-4">
                <Image
                  src="https://thewhitechamaleon.github.io/testrapp/images/home.png"
                  alt="home-icon"
                  className="home-icon"
                  onClick={handleHomeClick}
                />
              </div>
              {cardData && (
                <CardWithDragAndDrop
                  data={cardData}
                  widgetType="ObsolescenceWidget"
                />
              )}
            </div>
          </div>

          {isTableLoading ? (
            <div className="loading-indicator mt-5">
              <Loader />
            </div>
          ) : (
            <>
              <div className="wrapper-cta">
                <ObsolescenceWidgetToolbarNativeCta
                  onSave={handleSave}
                  type={type}
                  latestRevision={droppedObjectData?.cardData?.["Latest Revision"]}
                  droppedRevision={droppedObjectData?.cardData?.["Dropped Revision"]}
                  selectedRows={selectedTableRows}
                  state={stateVal}
                  tableData={selectableRows}
                  onMassUpdate={handleMassUpdate}
                />
                <ReusableTable
                  key={tableKey}
                  data={tableData}
                  columns={columns}
                  meta={{ updateTableData: setTableData }}
                  type={type}
                  latestRevision={droppedObjectData?.cardData?.["Latest Revision"]}
                  droppedRevision={droppedObjectData?.cardData?.["Dropped Revision"]}
                  widgetType="Obsolescence_Attribute_Widget"
                  onSelectableRowsChange={setSelectableRows}
                />
              </div>
            </>
          )}
        </>
      )}
    </>
  );
};

export default ObsolescenceWidget;
